const AccessWalletLayout = () => import('@/layouts/AccessWalletLayout');
const InterfaceLayout = () => import('@/layouts/InterfaceLayout');
const NotFoundLayout = () => import('@/layouts/NotFoundLayout');
const SendCurrencyContainer = () =>
  import('@/layouts/InterfaceLayout/containers/SendCurrencyContainer');
const EthWalletContainer = () =>
  import('@/layouts/WalletLayout/containers/EthWalletContainer');
const MobbyTokenWalletContainer = () =>
  import('@/layouts/WalletLayout/containers/MobbyTokenWalletContainer');
const WalletLayout = () => import('@/layouts/WalletLayout');

const router = [
  {
    path: '/access-my-wallet',
    name: 'AccessWalletLayout',
    component: AccessWalletLayout,
    meta: { requiresAuth: false }
  },
  {
    path: '/wallets',
    name: 'Wallets',
    component: WalletLayout,
    children: [
      {
        path: 'eth',
        name: 'Eth Wallet',
        component: EthWalletContainer
      },
      {
        path: 'mobby',
        name: 'Mobby Wallet',
        component: MobbyTokenWalletContainer
      }
    ]
  },
  {
    path: '/interface',
    component: InterfaceLayout,
    children: [
      {
        path: '',
        name: 'Main',
        component: SendCurrencyContainer
      },
      {
        path: 'send-transaction',
        name: 'Send Tx',
        component: SendCurrencyContainer
      }
    ]
  },
  {
    path: '*',
    name: '404',
    component: NotFoundLayout,
    meta: { requiresAuth: false }
  }
];

export default router;
